.write-tothehead {
    .input-name {
        width: 170px;
    }
    .bottom {
        border-top: none;
    }
    .body {
        padding-top: 50px;
        border-top: 1px dashed var(--Gainsboro);
    }
    .alert {
        margin-bottom: 30px;
        line-height: 25px;
    }
    &__head {
        margin-bottom: 47px;
        b {
            font-size: 17px;
        }
        span {
            display: block;
            margin-top: 10px;
            font-size: 14px;
            line-height: 21px;
        }
    }
}
@media(max-width: 767px) {
    .write-tothehead {
        .alert {
            margin-bottom: 25px;
        }
        &__head {
            margin-bottom: 27px;
        }
        .body {
            padding-top: 30px;
        }
    }
}
@media(max-width: 575px) {
    .write-tothehead {
        &__head {
            span {
                margin-top: 8px;
            }
        }
    }
}